export default (isProxy: boolean = true) => {
    if (process.env.STORYBOOK) {
        return '/mockData';
    }

    switch (process.env.ENV) {
        case 'prod':
        case 'rel':
            return '';
        // if  proxy  IT API
        // else  proxy u 
        //        <a>  u  false
        default:
            return isProxy ? '/ub2b-travel' : '//ub2b-travel.liontravel.com';
    }
};

export const member = (() => {
    switch (process.env.ENV) {
        case 'prod':
            return '//member.liontravel.com';
        case 'rel':
            return '//umember.liontravel.com';
        default:
            return '/umember';
    }
})();
